/* Error Dialog Overlay */
.error-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Error Dialog */
.error-dialog {
  background: white;
  border-radius: 20px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  animation: slideInUp 0.4s ease;
  position: relative;
  border: 2px solid transparent;
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Error Dialog Close Button */
.error-dialog-close {
  position: absolute;
  top: 20px;
  right: 20px;
  background: none;
  border: none;
  color: #718096;
  cursor: pointer;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  z-index: 10;
}

.error-dialog-close:hover {
  background: rgba(0, 0, 0, 0.1);
  color: #2d3748;
  transform: scale(1.1);
}

/* Error Dialog Icon */
.error-dialog-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 24px;
  position: relative;
}

.error-dialog-icon.error {
  background: linear-gradient(135deg, #f56565 0%, #e53e3e 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(245, 101, 101, 0.3);
}

.error-dialog-icon.success {
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(72, 187, 120, 0.3);
  animation: pulse 2s infinite;
}

.error-dialog-icon.info {
  background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(66, 153, 225, 0.3);
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* Error Dialog Content */
.error-dialog-content {
  text-align: center;
  padding: 0 40px 24px 40px;
}

.error-dialog-title {
  font-size: 24px;
  font-weight: 700;
  margin: 0 0 16px 0;
  color: #2d3748;
}

.error-dialog-message {
  font-size: 16px;
  line-height: 1.5;
  color: #4a5568;
  margin: 0;
}

/* Error Dialog Actions */
.error-dialog-actions {
  padding: 0 40px 40px 40px;
  text-align: center;
}

.error-dialog-btn {
  padding: 12px 32px;
  border: none;
  border-radius: 12px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 120px;
}

.error-dialog-btn.error {
  background: linear-gradient(135deg, #f56565 0%, #e53e3e 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(245, 101, 101, 0.3);
}

.error-dialog-btn.error:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(245, 101, 101, 0.4);
}

.error-dialog-btn.success {
  background: linear-gradient(135deg, #48bb78 0%, #38a169 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(72, 187, 120, 0.3);
}

.error-dialog-btn.success:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(72, 187, 120, 0.4);
}

.error-dialog-btn.info {
  background: linear-gradient(135deg, #4299e1 0%, #3182ce 100%);
  color: white;
  box-shadow: 0 8px 25px rgba(66, 153, 225, 0.3);
}

.error-dialog-btn.info:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(66, 153, 225, 0.4);
}

/* Responsive Design */
@media (max-width: 768px) {
  .error-dialog {
    width: 95%;
    margin: 20px;
  }
  
  .error-dialog-content {
    padding: 0 24px 16px 24px;
  }
  
  .error-dialog-title {
    font-size: 20px;
  }
  
  .error-dialog-message {
    font-size: 14px;
  }
  
  .error-dialog-actions {
    padding: 0 24px 24px 24px;
  }
  
  .error-dialog-btn {
    padding: 10px 24px;
    font-size: 14px;
    min-width: 100px;
  }
}
